<template>
  <div>
    <div class="header">
      <div style="font-size: 16px;line-height: 40px;padding: 0 0.3125rem;">
        <span @click="back">返回</span>
      </div>
      <div style="margin: 0 auto;text-align: center;padding: 10px 0;">
        <img :src="url.avatar_url" alt style="border-radius: 50%;margin: 0 auto;"/>
        <p style="font-size: 16px;">{{url.loginname}}</p>
      </div>
      <div style="font-size: 14px;padding: 0 0.3125rem;overflow: hidden;">
        <div class="leftTime" style="float: left;">
          <span>注册时间：</span>
          <span>{{url.create_at}}</span>
        </div>
        <div class="rightTime" style="float: right;">
          <span>积分：</span>
          <span>{{5}}</span>
        </div>
      </div>
    </div>
    <div class="main">
      <div>
        <ul class="list">
          <li
            v-for="(i,index) in arr"
            :key="index"
            @click="click(index)"
            :class="Isindex == index?'active':''"
          >{{i}}</li>
        </ul>
        <div>
          <div v-for="(i,index) in arr" :key="index">
            <div v-for="(item,index) in collect" :key="index" class="heightBox">
              <div class="imgUrl">
                <img :src="Isindex == 0?collect.author.avatar_url:item.author.avatar_url" alt />
              </div>
              <div class="rightContent">
                <p class="pContent">{{item.content}}</p>
                <p class="">{{Isindex == 0?collect.author.loginname:item.author.loginname}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      url: 1,
      arr: ["最近回复", "最新发布", "收藏话题"],
      Isindex: 0,
      collect: ""
    };
  },
  created() {
    this.$http
      .get("https://cnodejs.org/api/v1/user/alsotang")
      .then(response => {
        this.url = response.data.data;
      });
    this.click(0);
  },
  methods: {
    click(index) {
      this.Isindex = index;
      if (this.Isindex == 2) {
        this.$http
          .get("https://cnodejs.org/api/v1/topic_collect/alsotang")
          .then(data => {
            this.collect = data.data.data;
          });
      } else if (this.Isindex == 1) {
        this.collect = null;
      } else {
        this.$http
          .get("https://cnodejs.org/api/v1/topic/5433d5e4e737cbe96dcef312")
          .then(data => {
            this.collect = data.data.data;
            window.console.log( this.collect)
          });
      }
    },
    back(){
      this.$router.push({
        name: "index"
      });
    }
  }
};
</script>

