<template>
  <div>
    <header style="font-size: 16px;line-height: 60px;padding: 0 5px;">
      <span>全部</span>
    </header>
    <main>
      <div v-for="(item,index) in arr" :key="index" class="heightBox">
        <div class="imgUrl">
            <span>  </span>
          <img :src="item.author.avatar_url" alt />
        </div>
        <div class="rightContent">
          <p class="pContent">{{item.title}}</p>
          <p class>{{item.author.loginname}} <span class="rightspan">{{item.reply_count}}/{{item.visit_count}}</span></p>
          <p>
            <span>创建于:</span>
            {{item.create_at}}
          </p>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      arr: ""
    };
  },
  created() {
    this.$http.get("https://cnodejs.org/api/v1/topics").then(data => {
      window.console.log(data.body.data);
      this.arr = data.body.data;
    });
  }
};
</script>

<style scoped>
header {
  width: 100%;
  height: 1.2rem;
  background-color: #3c3b3b;
  color: #fff;
}
.heightBox {
  height: 2.3rem;
  font-size: 0.3rem;
  display: flex;
  border-bottom: 1px solid #ccc;
}
.imgUrl {
  width: 1.2rem;
  height: 1.2rem;
}
.imgUrl img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.rightContent {
  flex: 1;
  margin-left: 0.4rem;
}
.rightContent p {
  margin-top: 0.3rem;
}
.pContent {
  width: 5.5rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 800;
}
.rightspan{
    float: right;
}
</style>
